div {
    margin: 10px;
}
.result {
    display: grid;
    grid-template-columns: 1fr 2fr;
}
.result-title {
    grid-column: span 2;
}
.result-img {
    height: 200px;
    display: inline-block;
    border-radius: 30px;
}
.result-info {
    display: inline-block;
}
li {
    margin: 2px;
    padding: 8px;
    background-color: rgba(0,0,0,0.1);
    border-radius: 10px;
    -webkit-box-shadow: 1px 1px 0px rgba(0,0,0,0.9);
            box-shadow: 1px 1px 0px rgba(0,0,0,0.9);
}
body {
    font-family: Arial, Helvetica, sans-serif;
    background-color: #A0DDFF;
}
.App-title {
    display: inline;
    margin-left: 20px;
}
.keyword-form, .search-section, .result-display {
    border: solid 1px rgba(0,0,0,0.1);
    padding: 10px;
}


